import React from 'react';
import PropTypes from 'prop-types';

class AddBurgerForm extends React.Component {

  static propTypes = {
    AddBurgerForm: PropTypes.func
  };

  nameRef = React.createRef();
  priceRef = React.createRef();
  statusRef = React.createRef();
  descRef = React.createRef();
  imageRef = React.createRef();


  createBurger = (event) => {
    event.preventDefaut();
    const burger = {
      name: this.nameRef.current.value,
      price: parseFloat(this.priceRef.current.value || 0),
      status: this.statusRef.current.value,
      desc: this.descRef.current.value,
      image: this.imageRef.current.value,
    };
    this.props.AddBurger(burger);
    event.currentTarget.reset();
  }


  render (){
    return (
    <form className='burger-edit' onSubmit={this.createBurger}>
      <input ref={this.nameRef} name='name' 
      type='text' 
      placeholder='Name' 
      autoComplete='off'/>
      <input 
      ref={this.priceRef}
      name='price' 
      type='text' 
      placeholder='Price' 
      autoComplete='off'/>
      <select ref={this.statusRef} name='status' className='status'>
        <option value='available'>–î–æ—Å—Ç—É–ø–Ω–æ</option>
        <option value='unavailable'>–£–±—Ä–∞—Ç—å –∏–∑ –º–µ–Ω—é</option>
      </select>
      <textarea ref={this.sdescRef} name='desc' placeholder='Desc' />
      <input 
      ref={this.imageRef}
      name='image' 
      type='text' 
      placeholder='Image' 
      autoComplete='off'/>
      <button type='samit'>+ –î–æ–±–∞–≤–∏—Ç—å –≤ –º–µ–Ω—é</button>
    </form>
    );
  }
}

export default AddBurgerForm;