import React from "react";
import PropTypes from 'prop-types';

class Shipment extends React.Component{

  static propTypes = {
    total: PropTypes.number
  };


  render() {
    const {total} = this.props;
    const shipping = total >0 && total < 500 ? 350 : 99;
    const shippingNeon = shipping === 99 ? (
      <span className='font-effect-neon total_wrap-sheap'>
        {shipping} ‚ÇΩ
      </span>
    ) : (
      <span>{shipping} ‚ÇΩ</span>
    );
    return (
      <div className='total'>
        <div className='total_wrap'>
          <div>
            <div>–î–æ—Å—Ç–∞–≤–∫–∞: {total > 0 ? shippingNeon : null}</div>
            <div className='total_wrap-free'>
              {total < 500 ? `–ó–∞–∫–∞–∂–∏—Ç–µ –µ—â–µ –Ω–∞ ${500 - total} ‚ÇΩ –¥–ª—è –¥–æ—Å—Ç–∞–≤–∫–∏ –∑–∞ 99 ‚ÇΩ` : null}
            </div>
          </div>

          <div className='total_wrap-final'>–ò—Ç–æ–≥–∏: {total} ‚ÇΩ</div>
        </div>
      </div>
    );
  }
}

export default Shipment;