import React from 'react';
import PropTypes from 'prop-types';
import Header from './Header';
import Order from './Order';
import MenuAdmin from './MenuAdmin';
import Burger from './Burger';
import sampleBurgers from '../sample-burgers';
import base from '../base';
import firebase from 'firebase/app';
import SignIn from './Auth/SignIn';

class App extends React.Component {
  static propTypes = {
    match: PropTypes.object
  };

  state = {
    burgers: {},
    order: {}
  };

  componentDidMount() {
    const { params } = this.props.match;

    const localStorageRef = localStorage.getItem(params.restaurantId);
    if (localStorageRef) {
      this.setState({ order: JSON.parse(localStorageRef) });
    }

    this.ref = base.syncState(`${params.restaurantId}/burgers`, {
      context: this,
      state: 'burgers'
    });
  }

  componentDidUpdate() {
    const { params } = this.props.match;
    localStorage.setItem(params.restaurantId, JSON.stringify(this.state.order));
  }

  componentWillUnmount() {
    base.removeBinding(this.ref);
  }

  addBurger = burger => {
    // 1. –î–µ–ª–∞–µ–º –∫–æ–ø–∏—é –æ–±—ä–µ–∫—Ç–∞ state
    const burgers = { ...this.state.burgers };
    // 2. –î–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤—ã–π –±—É—Ä–≥–µ—Ä –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é burgers
    burgers[`burger${Date.now()}`] = burger;
    // 3. –ó–∞–ø–∏—Å–∞—Ç—å –Ω–∞—à –Ω–æ–≤—ã–π –æ–±—ä–µ–∫—Ç burgers –≤ state
    this.setState({ burgers });
  };

  updateBurger = (key, updatedBurger) => {
    // 1. –î–µ–ª–∞–µ–º –∫–æ–ø–∏—é –æ–±—ä–µ–∫—Ç–∞ state
    const burgers = { ...this.state.burgers };
    // 2. –û–±–Ω–æ–≤–ª—è–µ–º –Ω—É–∂–Ω—ã–π burger
    burgers[key] = updatedBurger;
    // 3. –ó–∞–ø–∏—Å–∞—Ç—å –Ω–∞—à –Ω–æ–≤—ã–π –æ–±—ä–µ–∫—Ç burgers –≤ state
    this.setState({ burgers });
  };

  deleteBurger = key => {
    // 1. –î–µ–ª–∞–µ–º –∫–æ–ø–∏—é –æ–±—ä–µ–∫—Ç–∞ state
    const burgers = { ...this.state.burgers };
    // 2. –£–¥–∞–ª—è–µ–º burger
    burgers[key] = null;
    // 3. –ó–∞–ø–∏—Å–∞—Ç—å –Ω–∞—à –Ω–æ–≤—ã–π –æ–±—ä–µ–∫—Ç burgers –≤ state
    this.setState({ burgers });
  };

  loadSampleBurgers = () => {
    this.setState({ burgers: sampleBurgers });
  };

  addToOrder = key => {
    // 1. –î–µ–ª–∞–µ–º –∫–æ–ø–∏—é –æ–±—ä–µ–∫—Ç–∞ state
    const order = { ...this.state.order };
    // 2. –î–æ–±–∞–≤–∏—Ç—å –∫–ª—é—á –∫ –∑–∞–∫–∞–∑—É —Å–æ –∑–Ω–∞—á-–µ–º 1, –ª–∏–±–æ –æ–±–Ω–æ–≤–∏—Ç—å —Ç–µ–∫—É—â. –∑–Ω–∞—á.
    order[key] = order[key] + 1 || 1;
    // 3. –ó–∞–ø–∏—Å–∞—Ç—å –Ω–∞—à –Ω–æ–≤—ã–π –æ–±—ä–µ–∫—Ç order –≤ state
    this.setState({ order });
  };

  deleteFromOrder = key => {
    // 1. –î–µ–ª–∞–µ–º –∫–æ–ø–∏—é –æ–±—ä–µ–∫—Ç–∞ state
    const order = { ...this.state.order };
    // 2. –£–¥–∞–ª—è–µ–º burger
    delete order[key];
    // 3. –ó–∞–ø–∏—Å–∞—Ç—å –Ω–∞—à –Ω–æ–≤—ã–π –æ–±—ä–µ–∫—Ç order –≤ state
    this.setState({ order });
  };

  handleLogout = async () => {
    await firebase.auth().signOut();
    window.location.reload();
  };

  render() {
    return (
      <SignIn>
        <div className='burger-paradise'>
          <div className='menu'>
            <Header title='Hot Burgers' />
            <ul className='burgers'>
              {Object.keys(this.state.burgers).map(key => {
                return (
                  <Burger
                    key={key}
                    index={key}
                    addToOrder={this.addToOrder}
                    details={this.state.burgers[key]}
                  />
                );
              })}
            </ul>
          </div>
          <Order
            deleteFromOrder={this.deleteFromOrder}
            burgers={this.state.burgers}
            order={this.state.order}
          />
          <MenuAdmin
            addBurger={this.addBurger}
            loadSampleBurgers={this.loadSampleBurgers}
            burgers={this.state.burgers}
            updateBurger={this.updateBurger}
            deleteBurger={this.deleteBurger}
            handleLogout={this.handleLogout}
          />
        </div>
      </SignIn>
    );
  }
}

export default App;
